"use client";
import { useRouter } from 'next/navigation';
import { useState } from 'react';

export default function AdminLogin() {
  const [token, setToken] = useState('');
  const [error, setError] = useState('');
  const router = useRouter();

  async function submit(e: React.FormEvent) {
    e.preventDefault();
    // Set cookie and try settings endpoint
    document.cookie = `admin=${token}; path=/; max-age=2592000; samesite=lax`;
    const res = await fetch('/api/admin/settings');
    if (res.ok) {
      router.replace('/admin');
    } else {
      setError('Invalid token');
    }
  }

  return (
    <div className="px-4 sm:px-6 py-12 max-w-sm mx-auto">
      <form onSubmit={submit} className="rounded-2xl p-6 bg-white/80 dark:bg-white/5 backdrop-blur border border-black/5 dark:border-white/10 grid gap-4">
        <div className="text-lg font-medium">Admin Login</div>
        <input
          type="password"
          value={token}
          onChange={(e) => setToken(e.target.value)}
          placeholder="Enter admin token"
          className="rounded-lg px-3 py-2 bg-white/60 dark:bg-black/30 border border-black/10 dark:border-white/10 focus:outline-none focus:ring-2 focus:ring-cyan-400/60"
        />
        {error && <div className="text-sm text-red-500">{error}</div>}
        <button type="submit" className="rounded-xl p-[2px] bg-gradient-to-br from-cyan-400/60 via-fuchsia-400/40 to-violet-500/60 hover:from-cyan-400 hover:to-violet-500 transition">
          <div className="rounded-[10px] bg-white/80 dark:bg-black/40 backdrop-blur-md py-2.5 text-center">Enter</div>
        </button>
      </form>
    </div>
  );
}

